<template>
  <b-list-group-item style='border: none'>
    <h6 class='mb-1'>
      <a :href='data.url'>{{data.title}} {{data.firstName}} {{data.lastName}}</a>
    </h6>
    <div class='d-flex w-100, justify-content-between'>
      <small class='text-muted'>
        <b-link @click="revealEditModal(data.advisorID)">Edit</b-link>
      </small>
      <small class='text-muted'>
        <b-link @click="revealRemoveModal(data.advisorID)">Remove</b-link>
      </small>
      <b-modal :id="'modal-advisor-edit-' + data.advisorID" hide-footer>
        <b-form>
          <AdviserAdviseeForm :data="data"></AdviserAdviseeForm>
        </b-form>
      </b-modal>
      <b-modal :id="'modal-advisor-remove-' + data.advisorID">
        <b-form @submit="$emit('delete-row')" hide-footer>
          <b-button type="submit" variant="danger">Are you sure?</b-button>
        </b-form>
      </b-modal>
    </div>
  </b-list-group-item>
</template>

<script>
/* eslint-disable */
import Vue from 'vue';

import { FormPlugin, ModalPlugin, ListGroupPlugin, ButtonPlugin, LinkPlugin } from 'bootstrap-vue';
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap-vue/dist/bootstrap-vue.css';

const AdviserAdviseeForm = () => import('../form/AdviserAdviseeForm');

Vue.use(ListGroupPlugin);
Vue.use(FormPlugin);
Vue.use(ModalPlugin);
Vue.use(ButtonPlugin);
Vue.use(LinkPlugin);

export default {
  name: 'AdviserAdvisee',
  props:['data'],
  components: {
    AdviserAdviseeForm,
  },
  methods:{
    async revealEditModal(_id){
      this.$bvModal.show('modal-advisor-edit-' + _id);
    },
    async revealRemoveModal(_id){
      this.$bvModal.show('modal-advisor-remove-' + _id);
    }
  },
}
</script>



